import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  ContentTitle,
  ContentSubtitle,
  FilterContainer,
  FilterButton,
  ListContainer,
  ListItem,
  ItemType,
  ItemTitle,
  ItemDetails,
  Pagination,
  PageButton,
  PageNumber
} from '../style';
import styled from 'styled-components';

const ActionButton = styled.button`
  padding: 8px 16px;
  background-color: #0051FF;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: background-color 0.2s;
  
  &:hover {
    background-color: #003DB8;
  }
`;

const feedbackOptions = [
  '  ',
  '   ',
  '  ',
  ',  ',
  '   ',
  '    ',
  '    ',
  '    ',
  '   ',
  '    ',
  '   ',
  '   ',
  '  ',
  '   ',
  '  ',
  '  ',
  '  ',
  ' /  ',
  '   ',
  '    ',
];

const PopupModalOverlay = styled.div`
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(33, 33, 33, 0.45);
  z-index: 1010;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const PopupModal = styled.div`
  background: #fff;
  max-width: 800px;
  width: 98vw;
  border-radius: 16px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.12);
  padding: 36px 38px 28px 38px;
  position: relative;
`;
const CloseButton = styled.button`
  position: absolute;
  top: 16px; right: 20px;
  border: none;
  background: none;
  font-size: 32px;
  color: #888;
  cursor: pointer;
`;
const PopupTitle = styled.h3`
  font-size: 20px;
  margin: 0 0 12px 0;
  font-weight: bold;
`;
const OptionGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 12px 0 22px 0;
`;
const OptionBtn = styled.button`
  flex: 1 1 30%;
  min-width: 32%;
  max-width: 33.3%;
  box-sizing: border-box;
  padding: 15px 8px;
  border-radius: 4px;
  border: 1px solid #ACB5BF;
  background: ${({selected})=>selected ?'#0051FF':'#F1F3F6'};
  color: ${({selected})=>selected?'#fff':'#515151'};
  font-size: 15px;
  margin-bottom: 6px;
  cursor: pointer;
  transition: background .15s, color .15s;
  justify-content: center;
  align-items: center;
  display: flex;
`;
const PopupFooter = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 10px;
`;

const MySomPartyContainer = () => {
  const navigate = useNavigate();
  const [activeFilter, setActiveFilter] = useState('completed');
  const [showPopup, setShowPopup] = useState(false);
  const [selected, setSelected] = useState([]);

  //  /  -> teamLeaderName  
  const teamLeaderName = 'zl';

  const handleOptionClick = (idx) => {
    if(selected.includes(idx)) setSelected(selected.filter(i=>i!==idx));
    else if(selected.length < 3) setSelected([...selected, idx]);
    // 3   
  };

  const challenges = [
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.07',
      repeat: '[] []',
      progress: '7/7'
    },
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.07',
      repeat: '[] []',
      progress: '7/7'
    }
  ];

  //        
  const getButtonLabel = () => {
    if (activeFilter === 'progress') return '';
    if (activeFilter === 'completed') return '';
    return null; //    
  };

  const getButtonPath = () => {
    if (activeFilter === 'progress') return '/main/my-page/my-som-check';
    if (activeFilter === 'completed') return '/main/my-page/my-som-review';
    return null;
  };

  return (
    <div>
      <ContentTitle>   ?</ContentTitle>
      <ContentSubtitle>     .</ContentSubtitle>
      
      <FilterContainer>
        <FilterButton
          active={activeFilter === 'scheduled'}
          onClick={() => setActiveFilter('scheduled')}
        >
           (2)
        </FilterButton>
        <FilterButton
          active={activeFilter === 'progress'}
          onClick={() => setActiveFilter('progress')}
        >
           (2)
        </FilterButton>
        <FilterButton
          active={activeFilter === 'completed'}
          onClick={() => setActiveFilter('completed')}
        >
          (15)
        </FilterButton>
      </FilterContainer>
      
      <ListContainer>
        {challenges.map((challenge, index) => (
          <ListItem key={index}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%' }}>
              <div>
                <ItemType>{challenge.type}</ItemType>
                <ItemTitle>{challenge.title}</ItemTitle>
                <ItemDetails>
                  <span>{challenge.date}</span>
                  <span>{challenge.repeat} {challenge.progress}</span>
                </ItemDetails>
              </div>

              {/*    ,      */}
              {getButtonLabel() && (
                activeFilter === 'progress' ? (
                  <ActionButton onClick={()=>navigate('/main/my-page/my-som-check')}>
                    {getButtonLabel()}
                  </ActionButton>
                ) : (
                  <ActionButton onClick={()=>setShowPopup(true)}>
                    {getButtonLabel()}
                  </ActionButton>
                )
              )}
            </div>
          </ListItem>
        ))}
      </ListContainer>

      <Pagination>
        <PageButton disabled>&lt; </PageButton>
        <PageNumber>1</PageNumber>
        <PageButton disabled={false}> &gt;</PageButton>
      </Pagination>

      {/*   */}
      {showPopup && (
        <PopupModalOverlay>
          <PopupModal>
            <CloseButton onClick={()=>setShowPopup(false)}></CloseButton>
            <PopupTitle>{teamLeaderName}    ! <span style={{fontSize:'14px',fontWeight:'normal',color:'#222'}}>( 3, )</span></PopupTitle>
            <div style={{fontSize:'13px',color:'#AAB6BF',margin:'0 0 8px'}}>       .</div>
            <OptionGrid>
              {feedbackOptions.map((option, i) => (
                <OptionBtn key={i} selected={selected.includes(i)} onClick={()=>handleOptionClick(i)}>
                  {option}
                </OptionBtn>
              ))}
            </OptionGrid>
            <PopupFooter>
              <ActionButton type="button" onClick={()=>{setShowPopup(false);setSelected([]);}}></ActionButton>
            </PopupFooter>
          </PopupModal>
        </PopupModalOverlay>
      )}
    </div>
  );
};

export default MySomPartyContainer;
