import React from "react";
import OrderStyle from "./style";


const OrderUserInfo = () => {

    return(
        <OrderStyle.UserInfoWrapper>
            <OrderStyle.UserInfoContainer>
                <OrderStyle.UserContainer>
                    <OrderStyle.UserInfoName>
                        <OrderStyle.UserName></OrderStyle.UserName>
                        <OrderStyle.UserInfoTag>
                            <OrderStyle.TagName> </OrderStyle.TagName>
                        </OrderStyle.UserInfoTag>
                        <OrderStyle.UserFix>
                            <OrderStyle.UserFixText> </OrderStyle.UserFixText>
                        </OrderStyle.UserFix>
                    </OrderStyle.UserInfoName>
                <OrderStyle.UserAddressContainer>
                    <OrderStyle.UserAddress>   47-6</OrderStyle.UserAddress>
                    <OrderStyle.UserAddress>010-1234-5678</OrderStyle.UserAddress>
                    <OrderStyle.UserAddressButton>
                        <OrderStyle.UserAddress>  </OrderStyle.UserAddress>
                        <img style={{width:"12px", height:"8px", marginLeft:"600px"}} src="/assets/icons/drop_down.svg" alt=" " />
                    </OrderStyle.UserAddressButton>
                </OrderStyle.UserAddressContainer>
                </OrderStyle.UserContainer>
            </OrderStyle.UserInfoContainer>
        </OrderStyle.UserInfoWrapper>
    );
};

export default OrderUserInfo;