import React, { useState } from 'react';
import {
  FormContainer,
  Title,
  Subtitle,
  FormSection,
  Label,
  Input,
  ButtonGroup,
  PrimaryButton,
  DateRow,
  Select,
  RadioGroup,
  RadioLabel,
  ImagePreview,
  FileInput,
  FileInfo,
  ActionButtons,
  SubmitButton,
  DeleteButton
} from './style';

const MyInfoContainer = () => {
  const [formData, setFormData] = useState({
    email: 'garlemy@naver.com',
    nickname: '',
    phone: '010-8795-4379',
    birthYear: '2000',
    birthMonth: '10',
    birthDay: '21',
    gender: 'male',
    postcode: '08457',
    address1: '  12 26',
    address2: '301'
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
  };

  return (
    <FormContainer>
      <Title>  ?</Title>
      <Subtitle>      !</Subtitle>

      <form onSubmit={handleSubmit}>
        <FormSection>
          <Label>ID ()</Label>
          <Input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            readOnly
          />
        </FormSection>

        <FormSection>
          <Label></Label>
          <Input
            type="text"
            name="nickname"
            value={formData.nickname}
            onChange={handleChange}
          />
        </FormSection>

        <FormSection>
          <Label></Label>
          <ButtonGroup>
            <Input
              type="tel"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              style={{ flex: 1 }}
            />
            <PrimaryButton type="button"> </PrimaryButton>
          </ButtonGroup>
        </FormSection>

        <FormSection>
          <Label></Label>
          <DateRow>
            <Select name="birthYear" value={formData.birthYear} onChange={handleChange}>
              {Array.from({ length: 100 }, (_, i) => 2024 - i).map(year => (
                <option key={year} value={year}>{year}</option>
              ))}
            </Select>
            <Select name="birthMonth" value={formData.birthMonth} onChange={handleChange}>
              {Array.from({ length: 12 }, (_, i) => i + 1).map(month => (
                <option key={month} value={month.toString().padStart(2, '0')}>{month}</option>
              ))}
            </Select>
            <Select name="birthDay" value={formData.birthDay} onChange={handleChange}>
              {Array.from({ length: 31 }, (_, i) => i + 1).map(day => (
                <option key={day} value={day.toString().padStart(2, '0')}>{day}</option>
              ))}
            </Select>
          </DateRow>
        </FormSection>

        <FormSection>
          <Label></Label>
          <RadioGroup>
            <RadioLabel>
              <input
                type="radio"
                name="gender"
                value="male"
                checked={formData.gender === 'male'}
                onChange={handleChange}
              />
              
            </RadioLabel>
            <RadioLabel>
              <input
                type="radio"
                name="gender"
                value="female"
                checked={formData.gender === 'female'}
                onChange={handleChange}
              />
              
            </RadioLabel>
          </RadioGroup>
        </FormSection>

        <FormSection>
          <Label></Label>
          <div style={{ marginBottom: '8px' }}>
            <ButtonGroup>
              <Input
                type="text"
                name="postcode"
                value={formData.postcode}
                readOnly
                style={{ flex: 1 }}
              />
              <PrimaryButton type="button"> </PrimaryButton>
            </ButtonGroup>
          </div>
          <Input
            type="text"
            name="address1"
            value={formData.address1}
            readOnly
            style={{ marginBottom: '8px' }}
          />
          <Input
            type="text"
            name="address2"
            value={formData.address2}
            onChange={handleChange}
          />
        </FormSection>

        <FormSection>
          <Label>  </Label>
          <ImagePreview></ImagePreview>
          <FileInput type="file" accept="image/*" />
          <FileInfo>  </FileInfo>
          <FileInfo> 50.0M    </FileInfo>
          <div style={{ display: 'flex', gap: '8px', marginBottom: '8px' }}>
            <button type="button" style={{ color: '#0051FF', border: 'none', background: 'none', cursor: 'pointer' }}>+  </button>
            <button type="button" style={{ color: '#FF6B6B', border: 'none', background: 'none', cursor: 'pointer' }}>-  </button>
          </div>
          <PrimaryButton type="button"></PrimaryButton>
        </FormSection>

        <ActionButtons>
          <SubmitButton type="submit"></SubmitButton>
          <DeleteButton type="button"> </DeleteButton>
        </ActionButtons>
      </form>
    </FormContainer>
  );
};

export default MyInfoContainer;
