import React from 'react';
import {
  ContentTitle,
  ContentSubtitle
} from '../style';
import styled from 'styled-components';

const StatusBox = styled.div`
  display: flex;
  align-items: center;
  padding: 24px;
  background-color: #F9F9F9;
  border-radius: 8px;
  margin-bottom: 40px;
  gap: 32px;
`;

const StatusText = styled.div`
  flex: 1;
`;

const StatusTitle = styled.div`
  font-size: 16px;
  color: #757575;
  margin-bottom: 8px;
`;

const StatusValue = styled.div`
  font-size: 32px;
  font-weight: 700;
  color: #111111;
`;

const StatusLabel = styled.div`
  font-size: 14px;
  color: #757575;
  margin-top: 4px;
`;

const RequirementBox = styled.div`
  padding: 20px;
  background-color: #0051FF;
  color: white;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
`;

const TableHeader = styled.div`
  font-size: 18px;
  font-weight: 700;
  color: #111111;
  margin-bottom: 16px;
`;

const InfoSection = styled.div`
  margin-top: 40px;
`;

const InfoTitle = styled.div`
  font-size: 16px;
  font-weight: 700;
  color: #111111;
  margin-bottom: 16px;
`;

const InfoList = styled.ol`
  padding-left: 24px;
  font-size: 14px;
  color: #111111;
  line-height: 1.8;
  
  li {
    margin-bottom: 8px;
  }
`;

const BulletList = styled.ul`
  padding-left: 24px;
  font-size: 14px;
  color: #111111;
  line-height: 1.8;
  
  li {
    margin-bottom: 8px;
  }
`;

const MySomRankContainer = () => {
  return (
    <div>
      <ContentTitle>   ?</ContentTitle>
      <ContentSubtitle>     .</ContentSubtitle>
      
      <StatusBox>
        <StatusText>
          <StatusTitle>  .</StatusTitle>
          <StatusValue>zl   Silver .</StatusValue>
          <StatusLabel>  6</StatusLabel>
        </StatusText>
        <RequirementBox>
          Gold  , 4  .
        </RequirementBox>
      </StatusBox>

      <TableHeader>  </TableHeader>
      <table style={{ width: '100%', borderCollapse: 'collapse', marginBottom: '32px' }}>
        <thead>
          <tr style={{ backgroundColor: '#F9F9F9' }}>
            <th style={{ padding: '12px', borderBottom: '2px solid #E0E0E0', textAlign: 'left', fontSize: '14px', fontWeight: '600' }}></th>
            <th style={{ padding: '12px', borderBottom: '2px solid #E0E0E0', textAlign: 'left', fontSize: '14px', fontWeight: '600' }}></th>
            <th style={{ padding: '12px', borderBottom: '2px solid #E0E0E0', textAlign: 'left', fontSize: '14px', fontWeight: '600' }}> </th>
            <th style={{ padding: '12px', borderBottom: '2px solid #E0E0E0', textAlign: 'left', fontSize: '14px', fontWeight: '600' }}> </th>
            <th style={{ padding: '12px', borderBottom: '2px solid #E0E0E0', textAlign: 'left', fontSize: '14px', fontWeight: '600' }}> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>Rookie</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>0</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>0%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>0%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}></td>
          </tr>
          <tr>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>Silver</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>10</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>10%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>5%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}></td>
          </tr>
          <tr>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>Gold</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>50</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>20%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>10%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}></td>
          </tr>
          <tr>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>Diamond</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>100</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>30%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>15%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}></td>
          </tr>
          <tr>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>Master</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>200</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>50%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}>25%</td>
            <td style={{ padding: '12px', borderBottom: '1px solid #E0E0E0' }}></td>
          </tr>
        </tbody>
      </table>

      <InfoSection>
        <InfoTitle>  </InfoTitle>
        <InfoList>
          <li>     .</li>
          <li>     .</li>
          <li>    .</li>
        </InfoList>
      </InfoSection>

      <InfoSection>
        <InfoTitle>  </InfoTitle>
        <BulletList>
          <li>       .</li>
        </BulletList>
      </InfoSection>
    </div>
  );
};

export default MySomRankContainer;
