import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  ContentTitle,
  ContentSubtitle,
  FilterContainer,
  FilterButton,
  ListContainer,
  ListItem,
  ItemType,
  ItemTitle,
  ItemDetails,
  Pagination,
  PageButton,
  PageNumber
} from '../style';
import styled from 'styled-components';

const ActionButton = styled.button`
  padding: 8px 16px;
  background-color: #0051FF;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: background-color 0.2s;
  
  &:hover {
    background-color: #003DB8;
  }
`;

const MySomSoloContainer = () => {
  const navigate = useNavigate();
  const [activeFilter, setActiveFilter] = useState('scheduled');

  const challenges = [
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.31',
      repeat: '[] []',
      progress: '4/4'
    },
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.31',
      repeat: '[] []',
      progress: '4/4'
    }
  ];

  //      
  const getButtonLabel = () => {
    if (activeFilter === 'progress') return '';
    //      
    return null;
  };

  //      
  const getButtonPath = () => {
    if (activeFilter === 'progress') return '/main/my-page/my-som-check';
    return null;
  };

  return (
    <div>
      <ContentTitle>   ?</ContentTitle>
      <ContentSubtitle>     .</ContentSubtitle>
      
      <FilterContainer>
        <FilterButton
          active={activeFilter === 'scheduled'}
          onClick={() => setActiveFilter('scheduled')}
        >
           (2)
        </FilterButton>
        <FilterButton
          active={activeFilter === 'progress'}
          onClick={() => setActiveFilter('progress')}
        >
           (2)
        </FilterButton>
        <FilterButton
          active={activeFilter === 'completed'}
          onClick={() => setActiveFilter('completed')}
        >
           (15)
        </FilterButton>
      </FilterContainer>
      
      <ListContainer>
        {challenges.map((challenge, index) => (
          <ListItem key={index}>
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              width: '100%'
            }}>
              <div>
                <ItemType>{challenge.type}</ItemType>
                <ItemTitle>{challenge.title}</ItemTitle>
                <ItemDetails>
                  <span>{challenge.date}</span>
                  <span>{challenge.repeat} {challenge.progress}</span>
                </ItemDetails>
              </div>

              {/*    ,   */}
              {getButtonLabel() && (
                <ActionButton onClick={() => navigate(getButtonPath())}>
                  {getButtonLabel()}
                </ActionButton>
              )}
            </div>
          </ListItem>
        ))}
      </ListContainer>

      <Pagination>
        <PageButton disabled>&lt; </PageButton>
        <PageNumber>1</PageNumber>
        <PageButton disabled={false}> &gt;</PageButton>
      </Pagination>
    </div>
  );
};

export default MySomSoloContainer;
