import React, { useState } from "react";
import S from "./style";
import ShopInfo from "./ShopInfo";
import ShopReview from "./ShopReview";
import ShopRelated from "./ShopRelated";
import { useNavigate } from "react-router-dom";

const Shop = () => {
  const [selectedImage, setSelectedImage] = useState(
    "/assets/images/shop_som_doll.png"
  );
  const [isLiked, setIsLiked] = useState(false);
  const [likeCount, setLikeCount] = useState(255);
  const [activeTab, setActiveTab] = useState("info");
  const [open, setOpen] = useState(false);
  const [count, setCount] = useState(1);

  const subImages = [
    "/assets/images/shop_detailSub_doll.png",
    "/assets/images/shop_detailSub_doll2.png",
    "/assets/images/shop_detailSub_doll3.png",
  ];

  const handleLikeClick = () => {
    setIsLiked((prev) => !prev);
    setLikeCount((prev) => (isLiked ? prev - 1 : prev + 1));
  };

  const navigate = useNavigate();

  const handleCount = (type) => {
    if (type === "minus" && count > 1) {
      setCount(count - 1);
    } else if (type === "plus") {
      setCount(count + 1);
    }
  };

  return (
    <S.Page>
      <S.DetailContainer>
        {/*    */}
        <S.Left>
          <S.MainImage>
            <img src={selectedImage} alt="  " />
          </S.MainImage>

          <S.SubImageArea>
            {subImages.map((src, i) => (
              <S.SubImage key={i} onClick={() => setSelectedImage(src)}>
                <img src={src} alt={`  ${i + 1}`} />
              </S.SubImage>
            ))}
          </S.SubImageArea>

          {/*  /  */}
          <S.InfoSection>
            <S.InfoTabs>
              <S.InfoTab
                $active={activeTab === "info"}
                onClick={() => setActiveTab("info")}
              >
                
              </S.InfoTab>
              <S.InfoTab
                $active={activeTab === "review"}
                onClick={() => setActiveTab("review")}
              >
                 22
              </S.InfoTab>
            </S.InfoTabs>

            <S.InfoDivider />

            {activeTab === "info" ? (
              <>
                <ShopInfo />
                <ShopRelated /> {/*     */}
              </>
            ) : (
              <>
                <ShopReview />
                <ShopRelated /> {/*   */}
              </>
            )}
          </S.InfoSection>
        </S.Left>

        {/*    */}
        <S.Right>
          <S.TagRow>
            <S.DetailNewTag>NEW</S.DetailNewTag>
            <S.DetailBestTag>BEST</S.DetailBestTag>
          </S.TagRow>

          <S.Title> </S.Title>
          <S.DetailPrice>23,000</S.DetailPrice>

          <S.DetailReviewWrap>
            <S.Icon src="/assets/icons/review.svg" alt=" " />
            <S.Text>4.8 (22)</S.Text>
          </S.DetailReviewWrap>

          <S.DeliveryRow>
            <S.Delivery></S.Delivery>
            <S.Divider />
            <S.DeliveryCharge>3,000</S.DeliveryCharge>
          </S.DeliveryRow>

          <S.DeliveryInfo>30,000    </S.DeliveryInfo>

          <S.CountWrap>
            <S.DeliveryCount></S.DeliveryCount>
            <S.CountBox>
              <S.CountBtn className="minus"
                onClick={() => handleCount("minus")}
                disabled={count === 1}
                $disabled={count === 1}
              >
                -
              </S.CountBtn>
              <S.CountNum>{count}</S.CountNum>
              <S.CountBtn className="plus" onClick={() => handleCount("plus")}>+</S.CountBtn>
            </S.CountBox>
          </S.CountWrap>

          <S.ProductDetailBar />

          <S.ProductRow>
            <S.ProductTotalCount> {count}</S.ProductTotalCount>
            <S.ProductTotalPrice>
              {(23000 * count).toLocaleString()}
            </S.ProductTotalPrice>
          </S.ProductRow>

          {/*  /  /  */}
          <S.ButtonRow>
            <S.ProductLikeButton onClick={handleLikeClick}>
              <img
                src={
                  isLiked
                    ? "/assets/icons/filedlike.svg"
                    : "/assets/icons/favorite.svg"
                }
                alt=""
              />
              <span>{likeCount}</span>
            </S.ProductLikeButton>

            <S.CartButton onClick={() => setOpen(true)}></S.CartButton>
            <S.PurchaseButton></S.PurchaseButton>
          </S.ButtonRow>
        </S.Right>
      </S.DetailContainer>

      {open && (
        <S.Overlay onClick={() => setOpen(false)}>
          <S.Dialog onClick={(e) => e.stopPropagation()}>
            <S.DialogMsg>  .</S.DialogMsg>
            <S.DialogBtns>
              <S.DialogBtnCancel onClick={() => setOpen(false)}>
                
              </S.DialogBtnCancel>
              <S.DialogBtnCart
                onClick={() => navigate("/main/my-page/my-shop/cart")}
              >
                
              </S.DialogBtnCart>
            </S.DialogBtns>
          </S.Dialog>
        </S.Overlay>
      )}
    </S.Page>
  );
};

export default Shop;
