import React from 'react';
import {
  ContentTitle,
  ContentSubtitle
} from '../style';
import styled from 'styled-components';

const SummaryContainer = styled.div`
  display: flex;
  gap: 24px;
  margin-bottom: 32px;
`;

const SummaryCard = styled.div`
  flex: 1;
  padding: 24px;
  background-color: #F9F9F9;
  border-radius: 8px;
  text-align: center;
`;

const SummaryLabel = styled.div`
  font-size: 16px;
  color: #757575;
  margin-bottom: 8px;
`;

const SummaryValue = styled.div`
  font-size: 24px;
  font-weight: 700;
  color: #111111;
`;

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 24px;
`;

const TableHeader = styled.th`
  padding: 12px;
  background-color: #F9F9F9;
  border-bottom: 2px solid #E0E0E0;
  text-align: left;
  font-size: 14px;
  font-weight: 600;
  color: #111111;
`;

const TableCell = styled.td`
  padding: 12px;
  border-bottom: 1px solid #E0E0E0;
  font-size: 14px;
  color: #111111;
`;

const TableRow = styled.tr`
  &:hover {
    background-color: #F9F9F9;
  }
`;

const MySomCandyContainer = () => {
  return (
    <div>
      <ContentTitle>  ?</ContentTitle>
      <ContentSubtitle>    .</ContentSubtitle>
      
      <SummaryContainer>
        <SummaryCard>
          <SummaryLabel> </SummaryLabel>
          <SummaryValue>2,500 </SummaryValue>
        </SummaryCard>
        <SummaryCard>
          <SummaryLabel> </SummaryLabel>
          <SummaryValue>0 </SummaryValue>
        </SummaryCard>
        <SummaryCard>
          <SummaryLabel> </SummaryLabel>
          <SummaryValue>2,500 </SummaryValue>
        </SummaryCard>
      </SummaryContainer>

      <div style={{ fontSize: '18px', fontWeight: '700', marginBottom: '16px' }}>
          
      </div>

      <Table>
        <thead>
          <tr>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
            <TableHeader></TableHeader>
          </tr>
        </thead>
        <tbody>
          <TableRow>
            <TableCell></TableCell>
            <TableCell>2025.09.19</TableCell>
            <TableCell>+100</TableCell>
            <TableCell></TableCell>
            <TableCell>2km   !!  (+100 )</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell>2025.09.19</TableCell>
            <TableCell>+500</TableCell>
            <TableCell></TableCell>
            <TableCell> !!!  (+500 )</TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell>2025.09.19</TableCell>
            <TableCell>+500</TableCell>
            <TableCell></TableCell>
            <TableCell> !!!  (+500 )</TableCell>
          </TableRow>
        </tbody>
      </Table>

      <div style={{ display: 'flex', justifyContent: 'center', gap: '16px', marginTop: '40px', fontSize: '16px' }}>
        <button style={{ border: 'none', background: 'none', color: '#BDBDBD', cursor: 'not-allowed' }}>
          &lt; 
        </button>
        <span style={{ fontWeight: '700', color: '#0051FF' }}>1</span>
        <button style={{ border: 'none', background: 'none', color: '#111111', cursor: 'pointer' }}>
           &gt;
        </button>
      </div>
    </div>
  );
};

export default MySomCandyContainer;
