import React from "react";
import IntroStyle from "./styleIntro";
import { useNavigate } from "react-router-dom";

const IntroBanner = () => {

    const navigate = useNavigate();

    const handleGoSom = () => {
        navigate("/main/som/all");
    };


    return (
        <IntroStyle.IntroBannerWarp>
            <IntroStyle.IntroBannerContainer>
                <IntroStyle.IntroBannerImage src="/assets/images/intro_main.png" alt="" />
                <IntroStyle.IntroBannerText1>
                    <p> <IntroStyle.IntroBannerText2>''</IntroStyle.IntroBannerText2>  </p>
                    <p>  </p>
                </IntroStyle.IntroBannerText1>
                <IntroStyle.IntroBannerButtonContainer>
                    <IntroStyle.IntroBannerButton onClick={handleGoSom}> </IntroStyle.IntroBannerButton>
                    <IntroStyle.IntroBannerButton onClick={handleGoSom}> </IntroStyle.IntroBannerButton>
                </IntroStyle.IntroBannerButtonContainer>
            </IntroStyle.IntroBannerContainer>
        </IntroStyle.IntroBannerWarp>

    );
};

export default IntroBanner;