import React from "react";
import S from "./style";

const ShopNumberSelect = ({ shopList, pageNumber, setPageNumber }) => {
  const totalPages = Math.ceil(shopList.length / 8); //  totalPages   .
  const groupSize = 10;

  //      
  const currentGroup = Math.floor((pageNumber - 1) / groupSize);
  const startPage = currentGroup * groupSize + 1;
  const endPage = Math.min(startPage + groupSize - 1, totalPages);

  return (
    <S.Wrapper>
      {/*   */}
      <S.PrevButton
        disabled={startPage === 1}
        onClick={() => setPageNumber(startPage - groupSize)}
      >
        &lt; 
      </S.PrevButton>
      <S.PageList>
        {/*   */}
        {Array.from({ length: endPage - startPage + 1 }, (_, i) => {
          const page = startPage + i;
          return (
            <button
              key={page}
              className={pageNumber === page ? "active" : ""}
              onClick={() => setPageNumber(page)}
            >
              {page}
            </button>
          );
        })}
      </S.PageList>

      {/*   */}
      <S.AfterButton
        disabled={endPage >= totalPages}
        onClick={() => setPageNumber(startPage + groupSize)}
      >
         &gt;
      </S.AfterButton>
    </S.Wrapper>
  );
};

export default ShopNumberSelect;
