import React, { useState } from 'react';
import {
  ContentTitle,
  ContentSubtitle,
  FilterContainer,
  FilterButton,
  ListContainer,
  ListItem,
  ItemType,
  ItemTitle,
  ItemDetails,
  Pagination,
  PageButton,
  PageNumber
} from '../style';

const MySomAuthContainer = () => {
  const [activeFilter, setActiveFilter] = useState('pending');

  const challenges = [
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.07',
      repeat: '[] []',
      progress: '7/7'
    },
    {
      type: '',
      title: '2km   !!',
      date: '2025.09.01 ~ 2025.09.07',
      repeat: '[] []',
      progress: '7/7'
    }
  ];

  return (
    <div>
      <ContentTitle>   ?</ContentTitle>
      <ContentSubtitle>     .</ContentSubtitle>
      
      <FilterContainer>
        <FilterButton
          active={activeFilter === 'pending'}
          onClick={() => setActiveFilter('pending')}
        >
           (2)
        </FilterButton>
        <FilterButton
          active={activeFilter === 'completed'}
          onClick={() => setActiveFilter('completed')}
        >
           (5)
        </FilterButton>
      </FilterContainer>
      
      <ListContainer>
        {challenges.map((challenge, index) => (
          <ListItem key={index}>
            <div>
              <ItemType>{challenge.type}</ItemType>
              <ItemTitle>{challenge.title}</ItemTitle>
              <ItemDetails>
                <span>{challenge.date}</span>
                <span>{challenge.repeat} {challenge.progress}</span>
              </ItemDetails>
            </div>
          </ListItem>
        ))}
      </ListContainer>

      <Pagination>
        <PageButton disabled>&lt; </PageButton>
        <PageNumber>1</PageNumber>
        <PageButton disabled={false}> &gt;</PageButton>
      </Pagination>
    </div>
  );
};

export default MySomAuthContainer;
