import React, { useState } from "react";
import * as S from "./style";
import { Link } from "react-router-dom";

const SignUp = () => {
  const [gender, setGender] = useState("");

  return (
    <S.SignUpContainer>
      {/* background */}
      <S.BackgroundBox />

      {/* sign-up box */}
      <S.SignUpBox>
        {/* logo */}
        <S.Logo>blue cotton</S.Logo>

        {/* input */}
        <S.Input type="text" placeholder=" " name="memberName" />
        <S.Input type="text" placeholder=" " name="memberNickName" />
        <S.Input type="text" placeholder=" " name="memberEmail" />
        <S.Input type="password" placeholder=" " name="memberPassword" />
        <S.Input type="password" placeholder=" " name="memberPasswordCheck" />

        {/* address */}
        <S.AddressBox>
          <S.AddressInput placeholder=" " />
          <S.SmallButton></S.SmallButton>
        </S.AddressBox>

        {/* gender */}
        <S.GenderSelectBox>
          <S.GenderOption
            selected={gender === ""}
            onClick={() => setGender("")}
          >
            
          </S.GenderOption>
          <S.GenderOption
            selected={gender === ""}
            onClick={() => setGender("")}
          >
            
          </S.GenderOption>
        </S.GenderSelectBox>

        {/* birth */}
        <S.DateInputBox>
          <S.DateInput />
          <S.SmallButton></S.SmallButton>
        </S.DateInputBox>

        {/* sign-up button */}
        <S.SignUpButton to="/login"> </S.SignUpButton>

        {/* divider */}
        <S.Divider />

        {/* social login */}
        <S.SocialButtons>
          <S.SocialButton to="/kakao-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/kakao.png`}
              alt="kakao"
            />
             
          </S.SocialButton>

          <S.SocialButton to="/google-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/google.png`}
              alt="google"
            />
             
          </S.SocialButton>

          <S.SocialButton to="/naver-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/naver.png`}
              alt="naver"
            />
             
          </S.SocialButton>
        </S.SocialButtons>

        {/* login */}
        <S.LoginText>
           ?{" "}
          <Link to="/login"> </Link>
        </S.LoginText>
      </S.SignUpBox>
    </S.SignUpContainer>
  );
};

export default SignUp;
