import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import * as S from "./style";

const PostModifyContent = () => {
  const { id } = useParams(); // URL   ID 
  const navigate = useNavigate();

  //   
  const [title, setTitle] = useState("");
  const [category, setCategory] = useState("");
  const [content, setContent] = useState("");
  const [file, setFile] = useState(null);
  const [fileName, setFileName] = useState("  ");

  //    ( API   )
  const dummyPosts = [
    {
      id: 1,
      title: "   ",
      category: "hobby",
      content:
        "    !     .",
      fileName: "son_analysis.png",
    },
    {
      id: 2,
      title: "  ",
      category: "health",
      content: "  7   .   ",
      fileName: "routine.jpg",
    },
  ];

  //        (useEffect)
  useEffect(() => {
    const post = dummyPosts.find((p) => p.id === Number(id));
    if (post) {
      setTitle(post.title);
      setCategory(post.category);
      setContent(post.content);
      setFileName(post.fileName);
    } else {
      alert("  .");
      navigate("/main/post/all");
    }
  }, [id, navigate]);

  //   
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    if (selectedFile) {
      setFile(selectedFile);
      setFileName(selectedFile.name);
    }
  };

  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    alert(`${id}   !\n: ${title}`);
    navigate(`/main/post/read/${id}`);
  };

  //  
  const handleCancel = () => {
    if (window.confirm(" ?    .")) {
      navigate(`/main/post/read/${id}`);
    }
  };

  return (
    <S.Container>
      {/*   */}
      <S.PageTitle>{id}  </S.PageTitle>

      {/*  */}
      <S.Form onSubmit={handleSubmit}>
        {/*  */}
        <S.FormRow>
          <label></label>
          <input
            type="text"
            placeholder="  "
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
        </S.FormRow>

        {/*  */}
        <S.FormRow>
          <label></label>
          <select
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            required
          >
            <option value=""> </option>
            <option value="study"></option>
            <option value="health"></option>
            <option value="social"></option>
            <option value="hobby"></option>
            <option value="life"></option>
            <option value="rookie"></option>
          </select>
        </S.FormRow>

        {/*  */}
        <S.FormGroup>
          <textarea
            placeholder="  "
            value={content}
            onChange={(e) => setContent(e.target.value)}
            maxLength={1000}
            required
          />
          <div className="char-count">{content.length}/1000</div>
        </S.FormGroup>

        {/*  */}
        <S.FileBox>
          <div className="file-row">
            <label></label>
            <div className="file-select">
              <input
                type="file"
                id="fileInput"
                onChange={handleFileChange}
                style={{ display: "none" }}
              />
              <button type="button" onClick={() => document.getElementById("fileInput").click()}>
                 
              </button>
              <span className="file-name">{fileName}</span>
            </div>
          </div>

          <p className="file-info"> 50MB    .</p>
        </S.FileBox>

        {/*  */}
        <S.ButtonBox>
          <button type="button" className="cancel" onClick={handleCancel}>
            
          </button>
          <button type="submit" className="submit">
             
          </button>
        </S.ButtonBox>
      </S.Form>
    </S.Container>
  );
};

export default PostModifyContent;
