import React, { useState } from "react";
import S from "./style.js";

const ShopInfo = () => {
  const [open, setOpen] = useState(true);

  return (
    <>

      <S.InfoImage>
        <img src="/assets/images/shop_detailSub_doll.png" alt="  " />
      </S.InfoImage>

      {open && (
        <>
          <S.InfoTextWrap>
            <S.InfoKicker>   </S.InfoKicker>
            <S.InfoTitle> </S.InfoTitle>
            <S.InfoDesc>      .</S.InfoDesc>
          </S.InfoTextWrap>

          <S.InfoImage>
            <img src="/assets/images/shop_detailSub_doll2.png" alt="  2" />
          </S.InfoImage>
          <S.InfoImage>
            <img src="/assets/images/shop_detailSub_doll3.png" alt="  3" />
          </S.InfoImage>
          <S.InfoImage>
            <img src="/assets/images/shop_detailSub_doll4.png" alt="  4" />
          </S.InfoImage>

          {/*      */}
          <S.SpecSection>
            <S.SpecTitle></S.SpecTitle>
            <S.SpecList>
              <S.SpecRow>
                <S.SpecLabel></S.SpecLabel>
                <S.SpecValue>330g</S.SpecValue>
              </S.SpecRow>
              <S.SpecRow>
                <S.SpecLabel> </S.SpecLabel>
                <S.SpecValue>270  350  200 (mm)</S.SpecValue>
              </S.SpecRow>
              <S.SpecRow>
                <S.SpecLabel></S.SpecLabel>
                <S.SpecValue>POLYESTER, COTTON</S.SpecValue>
              </S.SpecRow>
            </S.SpecList>
          </S.SpecSection>

          {/*    */}
          <S.CautionBox>
            <S.CautionTitle></S.CautionTitle>
            <S.CautionText>
              {`   .         .
                       .       .
                     .     .
                       .
                 .         .`}
            </S.CautionText>
          </S.CautionBox>

          {/*     */}
          <S.SellerSection>
            <S.SellerTitle> </S.SellerTitle>
            <S.SellerList>
              <S.SellerRow>
                <S.SellerLabel></S.SellerLabel>
                <S.SellerValue> </S.SellerValue>
              </S.SellerRow>
              <S.SellerRow>
                <S.SellerLabel></S.SellerLabel>
                <S.SellerValue>541-1234-1234</S.SellerValue>
              </S.SellerRow>
              <S.SellerRow>
                <S.SellerLabel></S.SellerLabel>
                <S.SellerValue>2025--1234</S.SellerValue>
              </S.SellerRow>
              <S.SellerRow>
                <S.SellerLabel></S.SellerLabel>
                <S.SellerValue></S.SellerValue>
              </S.SellerRow>
              <S.SellerRow>
                <S.SellerLabel></S.SellerLabel>
                <S.SellerValue>
                  1544-1234<br />
                  :   9 6 /   12 1<br />
                  bluecotton1234@naver.com
                </S.SellerValue>
              </S.SellerRow>
            </S.SellerList>
          </S.SellerSection>
        </>
      )}

    <S.ToggleBox type="button"
      aria-expanded={open}
      onClick={() => setOpen((v) => !v)}>
      <S.ToggleText>{open ? " " : " "}</S.ToggleText>
      <S.ToggleIcon
        src="/assets/icons/drop_down.svg"   
        alt="/ "
        $open={open}
      />
    </S.ToggleBox>
    </>
  );
};

export default ShopInfo;
