import React, { useState } from "react";
import { Link } from "react-router-dom";
import * as S from "./style";

const categories = [
  { name: "", path: "/main/post/all" },
  { name: "", path: "/main/post/study" },
  { name: "", path: "/main/post/health" },
  { name: "", path: "/main/post/social" },
  { name: "", path: "/main/post/hobbies" },
  { name: "", path: "/main/post/life-style" },
  { name: "", path: "/main/post/rookie" },
];

const filters = [
  { value: "latest", label: "" },
  { value: "view", label: "" },
  { value: "comment", label: "" },
  { value: "popular", label: "" },
];

const PostCategory = () => {
  const [activeCat, setActiveCat] = useState("");
  const [filter, setFilter] = useState("latest");

  return (
    <S.TopBar>
      {/* ===  (Link ) === */}
      <S.CategoryList>
        {categories.map((c) => (
          <S.CategoryLink
            key={c.name}
            to={c.path}
            $active={activeCat === c.name}
            onClick={() => setActiveCat(c.name)}
          >
            {c.name}
          </S.CategoryLink>
        ))}
      </S.CategoryList>

      {/* ===  === */}
      <S.FilterBox>
        <S.FilterSelect
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
        >
          {filters.map((f) => (
            <option key={f.value} value={f.value}>
              {f.label}
            </option>
          ))}
        </S.FilterSelect>
      </S.FilterBox>
    </S.TopBar>
  );
};

export default PostCategory;
