import React, { useState } from "react";
import * as S from "./style";

const PostWriteContent = () => {
  const [title, setTitle] = useState("");
  const [category, setCategory] = useState("");
  const [content, setContent] = useState("");
  const [file, setFile] = useState(null);

  const handleSubmit = (e) => {
    e.preventDefault();
    alert(" !");
  };

  const handleCancel = () => {
    window.history.back();
  };

  return (
    <S.Container>
      {/*   */}
      <S.PageTitle>  </S.PageTitle>

      {/*  */}
      <S.Form onSubmit={handleSubmit}>
        {/*  */}
        <S.FormRow>
          <label></label>
          <input
            type="text"
            placeholder="   "
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </S.FormRow>

        {/*  */}
        <S.FormRow>
          <label></label>
          <select
            value={category}
            onChange={(e) => setCategory(e.target.value)}
          >
            <option value="">    </option>
            <option value="study"></option>
            <option value="health"></option>
            <option value="social"></option>
            <option value="hobby"></option>
            <option value="life"></option>
            <option value="rookie"></option>
          </select>
        </S.FormRow>

        {/*  */}
        <S.FormGroup>
          <textarea
            placeholder="    ?      "
            value={content}
            onChange={(e) => setContent(e.target.value)}
            maxLength={1000}
          />
          <div className="char-count">{content.length}/1000</div>
        </S.FormGroup>

        {/*  */}
        <S.FileBox>
          <div className="file-row">
            <label></label>
            <div className="file-select">
              <button type="button"> </button>
              <span className="file-name">  </span>
            </div>
          </div>

          <p className="file-info"> 50.0M    </p>

          <div className="file-actions">
            <button type="button" className="add-btn">
              <img alt=" " />
               
            </button>
            <button type="button" className="remove-btn">
              <img alt=" " />
               
            </button>
          </div>
        </S.FileBox>

        {/*  */}
        <S.ButtonBox>
          <button type="button" className="cancel" onClick={handleCancel}>
            
          </button>
          <button type="submit" className="submit">
             
          </button>
        </S.ButtonBox>
      </S.Form>
    </S.Container>
  );
};

export default PostWriteContent;