import React, { useEffect, useState } from 'react';
import { MapMarker } from 'react-kakao-maps-sdk';
import S from './style';

const MapContainer = () => {
  //      state  
  const [markers, setMarkers] = useState([]);

  //    ( null,    )  
  const [center, setCenter] = useState(null); 

  //   (    true)
  const [isLoaded, setIsLoaded] = useState(false);

  //     
  // const [latitude, setLatitude] = useState(37.44937029089704);
  // const [longitude, setLongitude] = useState(126.65436049042351);

  //     
  // const address = "   152";

  //     
  const address = [
    { address: "   152" },
    { address: "   429" },
    { address: "   134 P&S TOWER ()" },
  ];

  useEffect(() => {                                                      
    // geocoder  ->    
    const geocoder = new window.kakao.maps.services.Geocoder();

    //     
    const tempMarkers = [];

    //    
    // geocoder.addressSearch(address, function(result, status) { 
    //   if(status === window.kakao.maps.services.Status.OK){ 
    //     setLatitude(parseFloat(result[0].y)); 
    //     setLongitude(parseFloat(result[0].x)); 
    //   }else{ 
    //     console.error("   ", status) 
    //   } 
    // });

    //    
    address.forEach((item) => {
      geocoder.addressSearch(item.address, (result, status) => {
        if (status === window.kakao.maps.services.Status.OK) {
          const latitude = parseFloat(result[0].y);
          const longitude = parseFloat(result[0].x);

          //  ,   
          tempMarkers.push({ latitude, longitude });

          //     
          if (tempMarkers.length === address.length) {
            setMarkers(tempMarkers); 
            setIsLoaded(true);      

            //     
            if (!center && tempMarkers.length > 0) {
              setCenter({
                lat: tempMarkers[0].latitude,
                lng: tempMarkers[0].longitude,
              });
            }
          }
        } else {
          console.error("   :", item.address);
        }
      });
    });
  }, []); // address , center  X

  if (!isLoaded || !center) return <div>  ...</div>;

  return (
    <S.MapContainer>
      <S.Content>
        <S.Title>   </S.Title>
        <S.MapAndListWrapper>

          {/*    */}
          <S.MapBox>
            {/*    */}
            <S.Map center={center} level={5}>
              {markers.map((marker, i) => (
                <MapMarker
                  key={i}
                  position={{ lat: marker.latitude, lng: marker.longitude }}
                />
              ))}
            </S.Map>
          </S.MapBox>

          {/*    */}
          <S.ListBox>
              
          </S.ListBox>

        </S.MapAndListWrapper>
      </S.Content>
    </S.MapContainer>
  );
};

export default MapContainer;
