import React from "react";
import * as S from "./style";
import { Link } from "react-router-dom";

const Login = () => {
  return (
    <S.LoginContainer>
      
      {/* background */}
      <S.BackgroundBox />

      {/* login box */}
      <S.LoginBox>
        
        {/* logo */}
        <S.Logo>blue cotton</S.Logo>
        
        {/* input */}
        <S.Input type="text" placeholder=" " name="memberEmail" />
        <S.Input type="password" placeholder=" " name="memberPassword" />
        
        {/* login button */}
        <S.LoginButton to="/main/som/all"></S.LoginButton>

        <S.Divider />

        {/* social login */}
        <S.SocialButtons>
          {/* kakao */}
          <S.SocialButton to="/kakao-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/kakao.png`}
              alt="kakao"
            />
             
          </S.SocialButton>

          {/* google */}          
          <S.SocialButton to="/google-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/google.png`}
              alt="google"
            />
             
          </S.SocialButton>
          
          {/* naver */}          
          <S.SocialButton to="/naver-login">
            <img
              src={`${process.env.PUBLIC_URL}/assets/icons/naver.png`}
              alt="naver"
            />
             
          </S.SocialButton>
        </S.SocialButtons>

        {/* find id/psw */}
        <S.FindInfoBox>
          <Link to="/find-password"> ?</Link>
          <Link to="/find-id">ID ?</Link>
        </S.FindInfoBox>

        {/* sign-up */}
        <S.SignUpText>
           ?{" "}
          <Link to="/sign-up"> </Link>
        </S.SignUpText>
      </S.LoginBox>
    </S.LoginContainer>
  );
};

export default Login;
