import React, { useState, useMemo } from "react";
import S from "./style";
import ShopList from "./ShopList";
import ShopNumberSelect from "./shopNumberSelect/ShopNumberSelect";

const ShopContainer = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [selected, setSelected] = useState("  ");
  const options = ["", "  ", "  ", "  ", ""];

  //   18
  const ALL_ITEMS = useMemo(
    () =>
      Array.from({ length: 18 }, (_, i) => ({
        id: i + 1,
        name: ` ${i + 1}`,
        imageUrl: `/assets/images/sample_${(i % 8) + 1}.png`,
        priceText: "10,000",
        score: "4.8",
        reviewCount: 22,
        likeCount: 255,
        isNew: true,
        isBest: i % 2 === 0,
      })),
    []
  );


  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = 8;

  const pagedItems = useMemo(() => {
    const start = (pageNumber - 1) * pageSize;
    return ALL_ITEMS.slice(start, start + pageSize);
  }, [ALL_ITEMS, pageNumber]);

  return (
    <S.Page>
      <S.Banner>
        <S.BannerTextBox>
          <S.BannerTitle></S.BannerTitle>
          <S.BannerDesc>      !</S.BannerDesc>
        </S.BannerTextBox>
      </S.Banner>

      <S.Container>
        <S.LeftFilter>
                  <S.FilterGroup>
          <S.CatagoryTopBar />
          <S.FilterTitle></S.FilterTitle>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
        </S.FilterGroup>

        <S.FilterGroup>
          <S.FilterTitle> </S.FilterTitle>
          <S.Label><S.Checkbox /> NEW</S.Label>
          <S.Label><S.Checkbox /> BEST</S.Label>
      </S.FilterGroup>

        <S.FilterGroup>
          <S.FilterTitle> </S.FilterTitle>
          <S.Label><S.Checkbox /> </S.Label>
          <S.Label><S.Checkbox /> </S.Label>
        </S.FilterGroup>
        </S.LeftFilter>

        <S.Main>
          <S.SortTopLine />

          <S.SortBar>
            <S.DropdownWrap>
              <S.DropdownButton onClick={() => setIsOpen((p) => !p)}>
                {selected}
                <S.ArrowIcon>
                  <img src="/assets/icons/arrow_drop_down.png" alt=" " />
                </S.ArrowIcon>
              </S.DropdownButton>

              {isOpen && (
                <S.DropdownList>
                  {options.map((option) => (
                    <S.DropdownItem
                      key={option}
                      $active={option === selected}
                      onClick={() => {
                        setSelected(option);
                        setIsOpen(false);
                      }}
                    >
                      {option}
                    </S.DropdownItem>
                  ))}
                </S.DropdownList>
              )}
            </S.DropdownWrap>

            <S.SortRight>
              <span></span>
              <span></span>
              <S.Total>{ALL_ITEMS.length} </S.Total>
            </S.SortRight>
          </S.SortBar>

          <S.SortBottomLine />

          {/*   8   (  2) */}
          <ShopList items={pagedItems} />


          <S.Pagination>
            <ShopNumberSelect
              shopList={ALL_ITEMS}       
              pageNumber={pageNumber}
              setPageNumber={setPageNumber}
            />
          </S.Pagination>
        </S.Main>
      </S.Container>
    </S.Page>
  );
};

export default ShopContainer;
